import { useEffect, useState, useCallback } from "react";
import axios from "axios";
import { useRouter } from "next/router";
import { extractClaims } from "@/utils/extractclaims";
import EditDocumentModal from "@/components/editdocumentmodal";
import UploadModal from "@/components/uploadmodal";

type TagDto = { name: string };

type DocumentDto = {
  id: number;
  title: string;
  accessType: string;
  createdDate: string;
  uploadedBy: string;
  tags: TagDto[];
};

type PagedResponse = {
  documents: DocumentDto[];
  totalPages: number;
};

export default function DashboardPage() {
  const [documents, setDocuments] = useState<DocumentDto[]>([]);
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [totalPages, setTotalPages] = useState(1);
  const [error, setError] = useState("");
  const [loading, setLoading] = useState(false);
  const [showModal, setShowModal] = useState(false);
  const [userRole, setUserRole] = useState("");
  const [userEmail, setUserEmail] = useState("");
  const [sessionReady, setSessionReady] = useState(false);
  const [editingDoc, setEditingDoc] = useState<DocumentDto | null>(null);
  const [shareModalDoc, setShareModalDoc] = useState<DocumentDto | null>(null);

  const router = useRouter();
  const token =
    typeof window !== "undefined" ? localStorage.getItem("token") : null;

  useEffect(() => {
    if (typeof window !== "undefined") {
      const storedToken = localStorage.getItem("token");
      console.log("📦 Token from localStorage:", storedToken);

      if (storedToken) {
        try {
          const { role, email } = extractClaims(storedToken);
          console.log("🔍 Extracted claims:", { role, email });
          setUserRole(role);
          setUserEmail(email);
          setSessionReady(true);
        } catch (err) {
          console.error("❌ Failed to extract claims:", err);
        }
      }
    }
  }, []);

  const handleLogout = () => {
    localStorage.removeItem("token");
    router.push("/");
  };

  const fetchDocuments = useCallback(async () => {
    if (!token) {
      router.push("/");
      return;
    }

    setLoading(true);
    try {
      const url = search.trim()
        ? `${process.env.NEXT_PUBLIC_API_URL}/documents/search`
        : `${process.env.NEXT_PUBLIC_API_URL}/documents`;

      const response = await axios.get<PagedResponse>(url, {
        headers: { Authorization: `Bearer ${token}` },
        params: { query: search, page },
      });

      const { documents, totalPages } = response.data || {};
      setDocuments(Array.isArray(documents) ? documents : []);
      setTotalPages(totalPages || 1);
      setError("");
    } catch (err: unknown) {
      console.error("❌ Failed to load documents:", err);
      setError("Failed to load documents.");
    } finally {
      setLoading(false);
    }
  }, [token, router, search, page]);

  const handleDownload = async (id: number, title: string) => {
    if (!token) return;

    try {
      const url = `${process.env.NEXT_PUBLIC_API_URL}/documents/download/${id}`;
      const response = await axios.get(url, {
        headers: { Authorization: `Bearer ${token}` },
        responseType: "blob",
      });

      const blob = new Blob([response.data as BlobPart], {
        type: "application/pdf",
      });
      const link = document.createElement("a");
      link.href = URL.createObjectURL(blob);
      link.download = `${title}.pdf`;
      link.click();
      URL.revokeObjectURL(link.href);
    } catch (err: unknown) {
      console.error("❌ Download failed:", err);
      alert("Failed to download document.");
    }
  };

  const handleEdit = (doc: DocumentDto) => {
    setEditingDoc(doc);
  };

  const handleDelete = async (id: number) => {
    if (!token) return;
    const confirmed = window.confirm(
      "Are you sure you want to delete this document?"
    );
    if (!confirmed) return;

    try {
      await axios.delete(`${process.env.NEXT_PUBLIC_API_URL}/documents/${id}`, {
        headers: { Authorization: `Bearer ${token}` },
      });
      fetchDocuments();
    } catch (err) {
      console.error("❌ Delete failed:", err);
      alert("Failed to delete document.");
    }
  };

  useEffect(() => {
    fetchDocuments();
  }, [fetchDocuments]);

  if (!sessionReady) return <p>Loading session...</p>;

  return (
    <div style={{ maxWidth: 1280, margin: "auto", paddingTop: "2rem" }}>
      <div
        style={{
          display: "flex",
          flexDirection: "column",
          alignItems: "flex-end",
          marginBottom: "1rem",
        }}
      >
        <span>
          <strong>Email:</strong> {userEmail}
        </span>
        <span>
          <strong>Role:</strong> {userRole}
        </span>
        <button onClick={handleLogout} style={{ marginTop: "0.5rem" }}>
          Logout
        </button>
      </div>

      <h2>Documents</h2>

      {["Admin", "Contributor"].includes(userRole) && (
        <button onClick={() => setShowModal(true)}>Upload new document</button>
      )}

      <input
        type="text"
        placeholder="Search by title or access type"
        value={search}
        onChange={(e) => setSearch(e.target.value)}
        style={{ width: "100%", marginBottom: "1rem", padding: "0.5rem" }}
      />

      {loading ? (
        <p>Loading...</p>
      ) : error ? (
        <p style={{ color: "red" }}>{error}</p>
      ) : (
        <table
          style={{
            width: "100%",
            borderCollapse: "collapse",
            border: "1px solid #ccc",
          }}
        >
          <thead>
            <tr>
              <th style={{ padding: "0.5rem", borderBottom: "1px solid #ccc" }}>
                Title
              </th>
              <th style={{ padding: "0.5rem", borderBottom: "1px solid #ccc" }}>
                Access
              </th>
              <th style={{ padding: "0.5rem", borderBottom: "1px solid #ccc" }}>
                Date
              </th>

              <th style={{ padding: "0.5rem", borderBottom: "1px solid #ccc" }}>
                Uploaded By
              </th>
              <th style={{ padding: "0.5rem", borderBottom: "1px solid #ccc" }}>
                Actions
              </th>
            </tr>
          </thead>
          <tbody>
            {documents.length > 0 ? (
              documents.map((doc) => (
                <tr key={doc.id}>
                  <td style={{ padding: "0.5rem" }}>{doc.title}</td>
                  <td style={{ padding: "0.5rem" }}>{doc.accessType}</td>
                  <td style={{ padding: "0.5rem" }}>
                    {new Date(doc.createdDate).toLocaleDateString()}
                  </td>

                  <td style={{ padding: "0.5rem" }}>{doc.uploadedBy}</td>
                  <td
                    style={{
                      padding: "0.5rem",
                      display: "flex",
                      gap: "0.5rem",
                    }}
                  >
                    {(userRole === "Admin" ||
                      userRole === "Contributor" ||
                      userRole === "Manager" ||
                      (userRole === "Viewer" &&
                        doc.accessType === "Public")) && (
                      <button onClick={() => handleDownload(doc.id, doc.title)}>
                        Download
                      </button>
                    )}
                    {(userRole === "Admin" || doc.uploadedBy === userEmail) && (
                      <button onClick={() => setShareModalDoc(doc)}>
                        🔗 Share
                      </button>
                    )}
                    {(userRole === "Admin" ||
                      (userRole === "Contributor" &&
                        doc.uploadedBy === userEmail)) && (
                      <>
                        <button onClick={() => handleEdit(doc)}>✏️ Edit</button>
                        <button onClick={() => handleDelete(doc.id)}>
                          🗑️ Delete
                        </button>
                      </>
                    )}
                  </td>
                </tr>
              ))
            ) : (
              <tr>
                <td
                  colSpan={5}
                  style={{ padding: "0.5rem", textAlign: "center" }}
                >
                  No documents available.
                </td>
              </tr>
            )}
          </tbody>
        </table>
      )}

      <div
        style={{
          marginTop: "1rem",
          display: "flex",
          justifyContent: "center",
          gap: "1rem",
        }}
      >
        <button
          onClick={() => setPage((p) => Math.max(p - 1, 1))}
          disabled={page === 1}
        >
          ← Previous
        </button>
        <span>
          Page {page} of {totalPages}
        </span>
        <button
          onClick={() => setPage((p) => Math.min(p + 1, totalPages))}
          disabled={page === totalPages}
        >
          Next →
        </button>
      </div>

      {showModal && token && (
        <UploadModal
          token={token}
          onClose={() => setShowModal(false)}
          onSuccess={fetchDocuments}
        />
      )}

      {editingDoc && token && (
        <EditDocumentModal
          token={token}
          document={editingDoc}
          userEmail={userEmail}
          userRole={userRole}
          onClose={() => setEditingDoc(null)}
          onSuccess={() => {
            setEditingDoc(null);
            fetchDocuments();
          }}
        />
      )}

      {shareModalDoc && token && (
        <ShareDocumentModal
          token={token}
          documentId={shareModalDoc.id}
          userEmail={userEmail}
          userRole={userRole}
          onClose={() => setShareModalDoc(null)}
        />
      )}
    </div>
  );
}
