import { useState, useEffect } from "react";
import axios from "axios";

type TagDto = { name: string };
type DocumentDto = {
  id: number;
  title: string;
  accessType: string;
  uploadedBy: string;
  tags: TagDto[];
};

type ShareEntry = {
  email: string;
  permission: "Read" | "Write";
};

type Props = {
  token: string;
  document: DocumentDto;
  userEmail: string;
  userRole: string;
  onClose: () => void;
  onSuccess: () => void;
};

export default function EditDocumentModal({
  token,
  document,
  userEmail,
  userRole,
  onClose,
  onSuccess,
}: Props) {
  const [title, setTitle] = useState(document.title);
  const [accessType, setAccessType] = useState(document.accessType);
  const [tags, setTags] = useState<TagDto[]>(document.tags || []);
  const [newTag, setNewTag] = useState("");
  const [shareEmail, setShareEmail] = useState("");
  const [sharePermission, setSharePermission] = useState<"Read" | "Write">(
    "Read"
  );
  const [sharedUsers, setSharedUsers] = useState<ShareEntry[]>([]);
  const [availableUsers, setAvailableUsers] = useState<
    { email: string; role: string }[]
  >([]);
  const [loading, setLoading] = useState(false);

  const apiBase = process.env.NEXT_PUBLIC_API_URL;

  useEffect(() => {
    fetchShares();
    fetchAvailableUsers();
  }, []);

  const fetchShares = async () => {
    try {
      const res = await axios.get<ShareEntry[]>(
        `${apiBase}/documents/${document.id}/shares`,
        { headers: { Authorization: `Bearer ${token}` } }
      );
      setSharedUsers(res.data || []);
    } catch (err) {
      console.error("❌ Failed to fetch shares:", err);
    }
  };

  const fetchAvailableUsers = async () => {
    try {
      const res = await axios.get<{ email: string; role: string }[]>(
        `${apiBase}/auth/users`
      );
      setAvailableUsers(res.data || []);
    } catch (err) {
      console.error("❌ Failed to load users:", err);
    }
  };

  const handleAddTag = () => {
    if (newTag.trim()) {
      setTags([...tags, { name: newTag.trim() }]);
      setNewTag("");
    }
  };

  const handleRemoveTag = (name: string) => {
    setTags(tags.filter((tag) => tag.name !== name));
  };

  const handleUpdate = async () => {
    setLoading(true);
    try {
      await axios.put(
        `${apiBase}/documents/${document.id}`,
        { title, accessType, tags },
        { headers: { Authorization: `Bearer ${token}` } }
      );
      onSuccess();
    } catch (err) {
      console.error("❌ Update failed:", err);
      alert("Failed to update document.");
    } finally {
      setLoading(false);
    }
  };

  const handleShare = async () => {
    if (!shareEmail) return;
    try {
      await axios.post(
        `${apiBase}/documents/${document.id}/share`,
        { emails: [shareEmail], permission: sharePermission },
        { headers: { Authorization: `Bearer ${token}` } }
      );
      setShareEmail("");
      fetchShares();
    } catch (err) {
      console.error("❌ Share failed:", err);
      alert("Failed to share document.");
    }
  };

  const handleRevoke = async (email: string) => {
    try {
      await axios.delete(`${apiBase}/documents/${document.id}/share`, {
        headers: { Authorization: `Bearer ${token}` },
        params: { email },
      });
      fetchShares();
    } catch (err) {
      console.error("❌ Revoke failed:", err);
      alert("Failed to revoke access.");
    }
  };

  return (
    <div
      style={{
        position: "fixed",
        top: 0,
        left: 0,
        width: "100%",
        height: "100%",
        backgroundColor: "rgba(0,0,0,0.5)",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        zIndex: 1000,
      }}
    >
      <div
        style={{
          backgroundColor: "#fff",
          padding: "2rem",
          borderRadius: "8px",
          width: "600px",
          maxHeight: "90vh",
          overflowY: "auto",
        }}
      >
        <h3>Edit Document</h3>

        <label>Title:</label>
        <input value={title} onChange={(e) => setTitle(e.target.value)} />

        <label>Access Type:</label>
        <select
          value={accessType}
          onChange={(e) => setAccessType(e.target.value)}
        >
          <option value="Public">Public</option>
          <option value="Private">Private</option>
          <option value="Restricted">Restricted</option>
        </select>

        <label>Tags:</label>
        <div style={{ display: "flex", gap: "0.5rem" }}>
          <input value={newTag} onChange={(e) => setNewTag(e.target.value)} />
          <button onClick={handleAddTag}>Add Tag</button>
        </div>
        <ul>
          {tags.map((tag, i) => (
            <li key={i}>
              {tag.name}{" "}
              <button onClick={() => handleRemoveTag(tag.name)}>❌</button>
            </li>
          ))}
        </ul>

        <div style={{ marginTop: "1rem" }}>
          <button onClick={handleUpdate} disabled={loading}>
            Save Changes
          </button>
          <button onClick={onClose} style={{ marginLeft: "1rem" }}>
            Cancel
          </button>
        </div>
      </div>
    </div>
  );
}
